                var row = $(this);
                //indice riga corrente
                var rowIndex = Number(row.data('index'));
                //prendo gli indici correnti delle colonne
                var indexes = row.sortable('toArray', {
                    attribute: 'data-index'
                }).map(function (x) { return Number(x); });
